















import PropInput from './PropInput'
import { QCheckbox } from 'quasar'
import { Component } from 'vue-property-decorator'

@Component({
  name: 'PaBooeleanProp',
  components: {
    QCheckbox
  }
})
export default class PaBooleanProp extends PropInput {
  onInput (value: boolean) {
    this.commit(value)
  }
}
