


















import PropInput from './PropInput'
import { Component } from 'vue-property-decorator'

@Component({
  name: 'PaPercentProp'
})
export default class PaPercent extends PropInput {
  onInput (value: number) {
    this.commit(value)
  }
  beforeMounted () {
    if (!this.prop.value) {
      this.prop.value.number = 10
    }
  }
}
