















import PropInput from './PropInput'
import { Component } from 'vue-property-decorator'

@Component({
  name: 'PaSizeProp'
})
export default class PaSizeProp extends PropInput {
  onInput (value: number) {
    this.commit(value)
  }
  beforeMounted () {
    if (!this.prop.value) {
      this.prop.value = 'sm'
    }
  }
}
